<template>
    <section>
        <div>
            </div>
        <el-upload action="http://upload.qiniup.com" :file-list=main_banners list-type="picture-card" :on-preview="handlePictureCardPreview" :on-remove="handleBannerRemove" :data="postData" :on-success="handleBannerSuccess">
            <i class="el-icon-plus"></i>
        </el-upload>

        <el-dialog :visible.sync="dialogVisible" size="tiny">
            <img width="100%" :src="previewImageUrl" alt="">
        </el-dialog>
        <el-input v-for="(item,index) in main_banners" v-model="main_banners[index].villa_id" style="width:150px;"></el-input>
        <div></div>
        <el-button @click="save" type="primary"></el-button>
        <el-button @click="cancel"></el-button>

    </section>
</template>

<script>
import base_image_url from '../../common/js/base_url'
export default {
    data() {
        return {
            previewImageUrl: '',
            dialogVisible: false,
            main_banners: [],
            villas:[],
            postData: {
                token: '93L43E91oA1cbC9k40ZK2eSeOCqxxjJz1SsL4NGv:AKd2pGlo_zch4BJBLr5wsWZLV5Y=:eyJzY29wZSI6IjUxYmllc2h1IiwiZGVhZGxpbmUiOjE1NTM1MTIwMDJ9'
            },
        }
    },
    methods: {
        handlePictureCardPreview(file) {
            this.previewImageUrl = file.url
            this.dialogVisible = true
        },
        handleBannerSuccess(res) {
            var imgurl = base_image_url + res.key
            var dic = {}
            dic.url = imgurl
            this.main_banners.push(dic)
            this.villas.push("")
        },
        handleBannerRemove(file, fileList) {
            var index = this.main_banners.indexOf(file)
            this.main_banners.splice(index, 1)
            this.villas.splice(index, 1)
        },
        save() {
            var banners = []
            for (var i in this.main_banners){
                var dic = {}
                dic.villa_id = this.main_banners[i].villa_id
                dic.img_src = this.main_banners[i].url
                banners.push(dic)
            }
            console.log(banners)
            console.log(JSON.stringify(banners))
            var para = JSON.stringify(banners)
            this.$http.post("https://ferrariwang.cn/homeparty/manage/setMainBanners", para, { headers: "Content-Type:application/json" }).then(function(response) {
                console.log(response)
                // for (var item in response.body.data.banner) {
                //     var imgurl = response.body.data.banner[item].img_src
                //     var id = response.body.data.banner[item].villa_id
                //     var dic = {}
                //     dic.url = imgurl
                //     dic.villa_id = id
                //     this.main_banners.push(dic)
                // }

            }).catch(function(error) {
                console.log(error)
            })

        },
        cancel() {
            this.$router.push({ name: '', path: '/table' })
        },
        getBanner() {
            this.$http.get("https://ferrariwang.cn/homeparty/mini-program/getMainBanner", { headers: "Content-Type:application/json" }).then(function(response) {
                console.log(response)
                for (var item in response.body.data.banner) {
                    var imgurl = response.body.data.banner[item].img_src
                    var id = response.body.data.banner[item].villa_id
                    var dic = {}
                    dic.url = imgurl
                    dic.villa_id = id
                    this.main_banners.push(dic)
                }

            }).catch(function(error) {
                console.log(error)
            })
        },

    },
    mounted() {
        this.getBanner()
    }
}
</script>


